@import "src/style/mixin";

.card-container {
  margin: 50px 0 50px 0;
  padding: 20px;
  border-radius: 10px;
  box-sizing: border-box;

  .card-img{
    height: 70px;
    width: 70px;
    border-radius: 50%;
    margin: 0 auto;
  }

  .description{
    padding: 0;

    &:last-child{
      padding: 0;
    }

    .description__tooltip{
      cursor: pointer;
      @include font(16px, 26px);
    }
  }
}
